<template>
    <InputResultGame
        :inputFlag="inputFlag"
        :resultFlag="resultFlag"
        :resultList="resultList"
        :inputList="inputList"
        @clickTileInput="clickTileInput"
        @clickTileResult="clickTileResult"
        @clickControlRefresh="gameRefreshLevel"
        @clickControlNext="gameNextLevel"
        @clickControlCheck="gameCheckResult"
    />
</template>

<script>
import tileMixin from '@/mixins/tileMixin';
import tileListMixin from '@/mixins/tileListMixin';
import dictionaryMixin from '@/mixins/dictionaryMixin';
import inputResultGameMixin from '@/mixins/inputResultGameMixin';
import InputResultGame from '@/components/InputResultGame'
import { arrayFrom } from '@/utils';

export default {
    mixins: [
        tileMixin,
        tileListMixin,
        dictionaryMixin,
        inputResultGameMixin,
    ],
    components: {
        InputResultGame,
    },
    data() {
        return {
            inputListMask: [],
        };
    },
    methods: {
        clickTileInput(inputTile) {
            this.tileBindByTile(this.tileListFindIdle(this.resultList), inputTile, true);
        },
        clickTileResult(resultTile) {
            if (this.resultFlag !== true && this.tileIsUsed(resultTile)) {
                this.tileUnbind(this.tileListFindById(this.inputList, resultTile.relationId));
                this.tileUnbind(resultTile, true);
            }
        },
        buildAssertResult() {
            const assertResult = this.dictionaryGetAlphabet();
            this.inputListMask = [...arrayFrom(assertResult).keys()]
                .sort(() => 0.5 - Math.random())
                .slice(-5);
            return assertResult;
        },
        buildInputList() {
            return arrayFrom(this.assertResult)
                .filter((v,i) => this.inputListMask.indexOf(i) >=0)
                .sort(() => 0.5 - Math.random())
                .map(
                (v, i) => {
                    return this.tileLetterCreate(i, v);
                }
            );
        },
        buildResultList() {
            return arrayFrom(this.assertResult).map((v, i) => {
                return this.tileLetterCreate(i, this.inputListMask.indexOf(i) >=0 ? null : v);
            });
        }
    },
}
</script>